@import "../../sass/main.scss";

.container__listing {
    background-color: #ADD8E6;
    min-height: 100vh;
    @include flexJustifyCenterAlignCenter;
    flex-direction: column;

    &__table {
        display: flex;
        flex-direction: column;
        

        &--search {
            display: flex;
            justify-content: flex-end;
        }
    }
}